@font-face {
  font-family: 'big_shoulders_display';
  src: url('../assets/fonts/bigshouldersdisplay-bold.eot');
  src: url('../assets/fonts/bigshouldersdisplay-bold.eot?#iefix') format('embedded-opentype'),
    url('../assets/fonts/bigshouldersdisplay-bold.woff2') format('woff2'),
    url('../assets/fonts/bigshouldersdisplay-bold.woff') format('woff'),
    url('../assets/fonts/bigshouldersdisplay-bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'lexend_deca';
  src: url('../assets/fonts/lexenddeca-regular.eot');
  src: url('../assets/fonts/lexenddeca-regular.eot?#iefix') format('embedded-opentype'),
    url('../assets/fonts/lexenddeca-regular.woff2') format('woff2'),
    url('../assets/fonts/lexenddeca-regular.woff') format('woff'),
    url('../assets/fonts/lexenddeca-regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
